import React, {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import {
  createUserWithEmailAndPassword,
  getAuth,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signOut,
} from "firebase/auth";
import { useNavigate, useLocation } from "react-router-dom";
import { Box, Typography } from "@mui/material";
import { initializeApp } from "firebase/app";
import FirebaseConfig from "../config/firebase-config";
import useAppSnackbar from "../hook/useAppSnackBar";

interface AuthContextProps {
  checkingAuth: boolean;

  userId: string;
  register: ({
    email,
    password,
  }: {
    email: string;
    password: string;
  }) => Promise<void>;
  logIn: ({
    email,
    password,
  }: {
    email: string;
    password: string;
  }) => Promise<void>;
  logOut: () => Promise<void>;
}

const AuthContext = createContext<AuthContextProps>({
  checkingAuth: true,

  userId: "",
  register: async () => {},
  logIn: async () => {},
  logOut: async () => {},
});

interface AuthContextProviderProps {
  children: React.ReactNode;
}

initializeApp(FirebaseConfig);

const AuthProvider = ({ children }: AuthContextProviderProps) => {
  const [checkingAuth, setCheckingAuth] = useState(true);
  const [userId, setUserId] = useState("");

  const navigate = useNavigate();
  const location = useLocation();
  const auth = getAuth();
  const { showSnackbarError } = useAppSnackbar();

  const needAuth = !["/login", "/register", "/forgot-password"].includes(
    location.pathname
  );

  const register = useCallback(
    async ({ email, password }: { email: string; password: string }) => {
      try {
        await createUserWithEmailAndPassword(auth, email, password);
      } catch (error) {
        if (String(error).includes("email-already-in-use")) {
          throw "Email đã tồn tại";
        } else if (
          String(error).includes("Password should be at least 6 characters")
        ) {
          throw "Mật khẩu phải có ít nhất 6 kí tự";
        } else {
          throw error;
        }
      }
    },
    [auth, showSnackbarError]
  );

  const logIn = useCallback(
    async ({ email, password }: { email: string; password: string }) => {
      try {
        await signInWithEmailAndPassword(auth, email, password);
      } catch (error) {
        if (String(error).includes("user-not-found")) {
          throw "Tài khoản không tồn tại";
        } else {
          throw error;
        }
      }
    },
    [auth, showSnackbarError]
  );

  const logOut = useCallback(async () => {
    try {
      await signOut(auth);
    } catch (error) {
      showSnackbarError(error);
    }
  }, [auth, showSnackbarError]);

  useEffect(() => {
    const authCheck = onAuthStateChanged(auth, (user) => {
      if (user) {
        setUserId(user.uid);
        setCheckingAuth(false);
        if (!needAuth) {
          navigate("/home");
        }
      } else {
        if (needAuth) {
          navigate("/login");
        }
      }
    });

    return authCheck;
  }, []);

  if (checkingAuth && needAuth)
    return (
      <Box className="w100 h100 df aic jcc">
        <Typography>Loading...</Typography>
      </Box>
    );

  return (
    <AuthContext.Provider
      value={{
        checkingAuth,

        userId,
        register,
        logIn,
        logOut,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const store = useContext(AuthContext);
  return store;
};

export default AuthProvider;
